* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Lobster", cursive;
}
.app {
  text-align: center;
}

.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

ul {
  list-style: none;
  width: 100%;
}

.tasks-item {
  border: 1px solid #d3d3d30f;
  margin-bottom: 15px;
  padding: 15px;
  border-radius: 5px;
}
.task-input {
  width: 90%;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 15px;
  border: none;
}
